.App{
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
  background-color:white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App-header{
  display:flex;
  flex-direction:column;
 font-size: 25px;
 font-family: 'Gentium Book Plus', serif;


}

.App-header a:hover{
color:#6B5B95!important;
}

.data{
  height:70vh;
  width: 60vw;
  background:auto;
  color: orangered;
  border-radius: 3%;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
  display: flex;
  flex-direction: column;
  justify-content: center;


}
@media only screen and (max-width: 600px) {
  body {
    background-color:rgba(255, 98, 0, 0.304);
  }
  .data a{
  margin: 30px;
 
  }
  .data{
    height:60vh;
    width: 70vw;
   
    color: orangered;
    border-radius: 3%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    
  
  }
  .App-header{
    display:flex;
    flex-direction:column;
   font-size: 15px;
   font-family: 'Gentium Book Plus', serif;
  
  
  }
}
